.index-form {
  display: flex;
  gap: 16px;
  padding: 8px 0;
}
@media (max-width: 1280px) {
  .index-form {
    flex-direction: column;
  }
}

.index-dates {
  display: flex;
  gap: 16px;
}
@media (max-width: 767px) {
  .index-dates {
    flex-direction: column;
  }
}

.index-params {
  display: flex;
  gap: 16px;
}
@media (max-width: 767px) {
  .index-params {
    flex-direction: column;
  }
}

.index-datepicker {
  width: 152px;
}
@media (max-width: 1055px) {
  .index-datepicker {
    width: auto;
  }
}
@media (max-width: 767px) {
  .index-datepicker {
    width: 100%;
    max-width: none !important;
  }
}

.index-search-input {
  width: 163px;
}
.index-search-input_not-auth {
  width: 280px;
}
@media (max-width: 1055px) {
  .index-search-input {
    max-width: 240px;
    width: auto;
  }
}
@media (max-width: 767px) {
  .index-search-input {
    max-width: none;
  }
}

.index-params-select {
  min-width: 152px;
}
@media (max-width: 1055px) {
  .index-params-select {
    width: 240px;
  }
}
@media (max-width: 767px) {
  .index-params-select {
    width: auto;
  }
}

@media (max-width: 767px) {
  .index-submit {
    max-width: 496px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 16px;
  }
  .index-submit button {
    justify-content: center;
  }
}

.index-table {
  grid-template-columns: 124fr 148fr 504fr 128fr 56fr 88fr 56fr;
}
.index-table .table-row .table-cell:nth-child(5),
.index-table .table-row .table-cell:nth-child(7) {
  padding: 4px 8px;
  display: flex;
  align-items: center;
}

@media (max-width: 767px) {
  .index-table {
    grid-template-columns: 1fr;
  }
  .index-table .table-row {
    display: grid;
    grid-template-columns: 232fr 40fr 40fr;
    grid-template-areas: "consent status status" "name name name" "phone phone phone" "date cancel print";
  }
  .index-table .table-row .table-cell:nth-child(1) {
    grid-area: date;
    padding: 4px 0;
    border-bottom: 1px solid #d6d6d6;
    color: #5c5c5c;
  }
  .index-table .table-row .table-cell:nth-child(2) {
    grid-area: phone;
    padding: 0;
    color: #5c5c5c;
  }
  .index-table .table-row .table-cell:nth-child(3) {
    grid-area: name;
    padding: 8px 0 4px;
  }
  .index-table .table-row .table-cell:nth-child(4) {
    grid-area: consent;
    padding: 6px 0;
    font-size: 12px;
    line-height: 16px;
  }
  .index-table .table-row .table-cell:nth-child(5) {
    grid-area: cancel;
    padding: 0;
    border-bottom: 1px solid #d6d6d6;
  }
  .index-table .table-row .table-cell:nth-child(6) {
    grid-area: status;
    justify-content: flex-end;
    padding: 4px 0;
  }
  .index-table .table-row .table-cell:nth-child(7) {
    grid-area: print;
    padding: 0;
    border-bottom: 1px solid #d6d6d6;
  }
}
.index-pagination {
  display: flex;
  flex-wrap: nowrap;
  gap: 4px;
  padding: 16px 0;
  margin-left: auto;
  margin-right: auto;
}
.index-pagination__button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  border: none;
  border-radius: 8px;
  background-color: transparent;
  width: 40px;
  height: 40px;
  cursor: pointer;
  color: #0190db;
  text-align: center;
  font-size: 16px;
  line-height: 24px;
  transition: all 0.2s;
}
.index-pagination__button:hover:not(.index-pagination__button_disabled) {
  color: #006fbc;
}
.index-pagination__button_active {
  color: #5c5c5c;
  background-color: rgba(1, 144, 219, 0.08);
}
.index-pagination__button_disabled {
  color: #8f8f8f;
  cursor: default;
}
@media (max-width: 767px) {
  .index-pagination__button_desktop {
    display: none;
    visibility: hidden;
  }
}
.index-pagination__icon {
  width: 24px;
  height: 24px;
  fill: currentColor;
}
.index-pagination__icon_right {
  transform: rotate(180deg);
}

@media (max-width: 767px) {
  .index-search-input_not-auth {
    width: 100%;
  }
}

@media (max-width: 767px) {
  .index-qr_content {
    display: flex;
    justify-content: center;
  }
}
.index-qr_content__link {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 4px;
  min-height: 36px;
  padding: 0 16px;
  border: none;
  background-color: transparent;
  color: #0190db;
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
  transition: color 0.2s;
  cursor: pointer;
}
.index-qr_content__link:hover {
  color: #00539d;
}
.index-qr_content__icon {
  width: 24px;
  height: 24px;
  fill: currentColor;
}

.index-table-not-auth {
  grid-template-columns: 124fr 148fr 628fr 128fr 76fr;
}
@media (max-width: 767px) {
  .index-table-not-auth {
    grid-template-columns: 1fr;
    margin-top: 8px;
    margin-bottom: 16px;
  }
  .index-table-not-auth .table-row {
    display: grid;
    grid-template-columns: 248fr 80fr;
    grid-template-areas: "consent status" "name name" "phone phone" "date date";
  }
  .index-table-not-auth .table-row .table-cell:nth-child(1) {
    grid-area: date;
    padding: 12px 0 4px;
    border-bottom: 1px solid #d6d6d6;
    color: #5c5c5c;
  }
  .index-table-not-auth .table-row .table-cell:nth-child(2) {
    grid-area: phone;
    padding: 0;
  }
  .index-table-not-auth .table-row .table-cell:nth-child(3) {
    grid-area: name;
    padding: 8px 0 4px;
  }
  .index-table-not-auth .table-row .table-cell:nth-child(4) {
    grid-area: consent;
    padding: 0;
    font-size: 12px;
    line-height: 16px;
  }
  .index-table-not-auth .table-row .table-cell:nth-child(5) {
    grid-area: status;
    padding: 4px 0;
    justify-content: flex-end;
  }
}
.index-table-not-auth .table-row .table-cell:nth-child(5),
