  height: 8px;
  width: var(--percent-value);
  background-color: #75b872;
}
.graph__fill-bar::before {
  content: "";
  position: absolute;
  left: -7.5px;
  top: 0;
  width: 7.5px;
  height: 8px;
  background-color: #75b872;
  border-radius: 99px 0 0 99px;
}
.graph__fill-bar::after {
  content: "";
  position: absolute;
  right: 0;
  top: -4px;
  width: 1px;
  height: 16px;
  background-color: #75b872;
}
.graph__marks {
  height: 4px;
  margin: 0 7.5px;
  display: flex;
  justify-content: space-between;
}
.graph__mark {
  width: 1px;
  height: 4px;
  border-radius: 0 0 99px 99px;
  background-color: rgba(0, 0, 0, 0.16);
}
.graph__percent {
  position: absolute;
  top: 12px;
  width: 32px;
  color: #8f8f8f;
  text-align: center;
  font-size: 10px;
  line-height: 14px;
}
.graph__percent.start {
  left: 0;
}
.graph__percent.end {
  right: 0;
}
.graph__tooltip-wrapper {
  position: absolute;
  left: 0;
  right: 0;
  margin-left: 7.5px;
  margin-right: 7.5px;
}
.graph__tooltip {
  position: absolute;
  bottom: -18px;
  left: var(--percent-value);
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 82px;
  height: 40px;
  box-sizing: border-box;
  padding: 8px 12px;
  border-radius: 8px;
  background: rgba(0, 0, 0, 0.64);
  color: #fff;
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
}
.graph__tooltip.align-left {
  transform: translate(calc(-100% + 15px), 100%);
}
.graph__tooltip.align-left .graph__tooltip-arrow {
  right: 10px;
}
.graph__tooltip.align-right {
  transform: translate(-15px, 100%);
}
.graph__tooltip.align-right .graph__tooltip-arrow {
  left: 10px;
}
.graph__tooltip.align-center {
  transform: translate(-50%, 100%);
}
.graph__tooltip.align-center .graph__tooltip-arrow {
  left: 33.5px;
}
.graph__tooltip-arrow {
  position: absolute;
  top: -6px;
  gap: 4px;
  width: 12px;
  height: 6px;
  fill: rgba(0, 0, 0, 0.64);
  transform: rotate(180deg);
}
.graph__tooltip-icon {
  fill: rgba(255, 255, 255, 0.64);
}

